import React, { useState, useEffect } from 'react'
import Filter from './components/Filter'
import PersonForm from './components/PersonForm'
import Persons from './components/Persons'
import personService from './services/persons'
import Notification from './components/Notification'

const App = () => {
  const [persons, setPersons] = useState([])
  const [newName, setNewName] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [filter, setFilter] = useState('')
  const [noteficationMessage, setNotificationMessage] = useState(null)
  const [notificationIsError, setNotificationIsError] = useState(false)

  useEffect(() => {
    personService
      .getAll()
      .then(response => {
        setPersons(response)
      })
  }, [])

  const handleNameInputChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumberInputChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleFilterChange = (event) => {
    setFilter(event.target.value)
  }

  const handleSubmit = (event) => {
    event.preventDefault()
    const names = persons.map(person => person.name)
    if (names.includes(newName)) {
      if (window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)) {
        const personToUpdate = persons.find(person => 
          person.name === newName
        )
        personService
          .update(
            personToUpdate.id, 
            {...personToUpdate, number: newNumber}
          ).then(response => {
            setPersons(persons.map(person => 
              person.id === personToUpdate.id
              ? response : person
              )
            )
            setNotificationIsError(false)
            setNotificationMessage(`Updated number for ${newName}`)
            setTimeout(() => {
              setNotificationMessage(null)
            }, 5000)
          })
          .catch(error => {
            setNotificationIsError(true)
            setNotificationMessage(`${newName} has already been removed`)
            setTimeout(() => {
              setNotificationMessage(null)
            }, 5000)
            setPersons(persons.filter(person => person.id !== personToUpdate.id ))
          })
      }
    } else {
      const newPerson = {
        name: newName,
        number: newNumber
      }
      personService
        .create(newPerson)
        .then(response => {
          setPersons(persons.concat(response))
          setNotificationIsError(false)
          setNotificationMessage(`Added ${newName}`)
          setTimeout(() => {
            setNotificationMessage(null)
          }, 5000)
        })
    }
    setNewName('')
    setNewNumber('')
  }

  const removePerson = (personToRemove) => {
    if (window.confirm(`Remove ${personToRemove.name} from phonebook?`)) {
    personService
      .remove(personToRemove.id)
      .then(response => {
        setPersons(
          persons.filter(person => person.id !== personToRemove.id)
        )
        setNotificationIsError(false)
        setNotificationMessage(`Removed ${personToRemove.name}`)
        setTimeout(() => {
          setNotificationMessage(null)
        }, 5000)
      })
      .catch(error => {
        setPersons(persons.filter(person => person.id !== personToRemove.id))
      })
    }
  }

  return (
    <div>
      <h1>Phonebook</h1>
      <Notification 
        message={noteficationMessage} 
        error={notificationIsError}
      />
      <Filter filter={filter} onChange={handleFilterChange}/>

      <h2>Add a new number</h2>
      <PersonForm newName={newName} newNumber={newNumber}
        onNameChange={handleNameInputChange} 
        onNumberChange={handleNumberInputChange}
        handleSubmit={handleSubmit}/>

      <h2>Numbers</h2>
      <Persons persons={persons} filter={filter} 
      removePerson={removePerson}/>
    </div>
  )

}

export default App